import React, { RefAttributes, useMemo } from 'react';
import { PressEvent } from '@react-types/shared';
import { useHover, usePress, useFocusRing } from 'react-aria';
import { AnimatePresence, motion } from 'framer-motion';
import { mergeProps } from 'libs/merge-props';
import { clsx } from 'libs/clsx';
import { Icon, MonochromeIconDefinition, IconColorProvider } from 'components/icons';
import { Spacer } from 'components/layout';
import { transitions } from 'hooks/transitions';
import { InverseThemeSemantics, useTheme } from 'components/theme-provider';

import { createGenericForwardRefsFactory } from 'libs/generic-forward-refs';
import { BaseColorName, ColorTone } from 'libs/theme';

import styles from './button-v2.less';

interface ButtonV2Props extends RefAttributes<HTMLAnchorElement | HTMLButtonElement> {
  disabled?: boolean;
  validating?: boolean;
  onPress?: (event: PressEvent) => void;
  size?: 'sm' | 'md' | 'lg';
  color?: 'primary' | 'v2secondary' | 'v2tertiary' | 'neutral' | 'negative';
  leadingIcon?: MonochromeIconDefinition;
  trailingIcon?: MonochromeIconDefinition;
  className?: string;
}

/**
 * Determines a _default_ color for icons nested in a `<ButtonV2>`.
 */
function useIconColor(baseColor?: BaseColorName, inverseSemantics?: boolean, isHovered?: boolean, isPressed?: boolean) {
  const theme = useTheme({ inverseSemantics });

  return useMemo(() => {
    let iconBaseColor: BaseColorName | undefined;

    switch (baseColor) {
      case 'v2secondary':
      case 'v2tertiary':
        iconBaseColor = 'primary';
        break;

      case 'neutral':
        iconBaseColor = 'dark';
        break;

      case 'primary':
      case 'negative':
        iconBaseColor = 'light';
        break;

      default:
        iconBaseColor = undefined;
    }

    return iconBaseColor ? theme.hex[iconBaseColor].base : undefined;
  }, [theme.key, baseColor]);
}

const createButtonV2Components = /* @__PURE__ */ createGenericForwardRefsFactory(['button', 'a']);
const ButtonV2Components = /* @__PURE__ */ createButtonV2Components<ButtonV2Props>(
  'ButtonV2',

  { color: 'primary', size: 'md' },

  (props, externalRef) => {
    const {
      onPress,
      className,
      disabled,
      validating,
      size,
      color,
      leadingIcon,
      trailingIcon,
      children,
      component,
      ...otherProps
    } = props;

    const { isFocusVisible, focusProps } = useFocusRing();
    const { isHovered, hoverProps } = useHover({ isDisabled: disabled });
    const { isPressed, pressProps } = usePress({ onPress, isDisabled: disabled });
    const getScaleProps = transitions.useScaleIn();

    const inverseSemantics = false;
    const iconColor = useIconColor(color, inverseSemantics, isHovered, isPressed);

    const content = (
      <>
        <AnimatePresence>
          {isFocusVisible && <motion.span aria-hidden="true" className={styles.focusRing} {...getScaleProps(1.08)} />}
        </AnimatePresence>

        {leadingIcon && (
          <>
            <Icon size={size === 'sm' ? 16 : 24} type={leadingIcon} />
            <Spacer size={8} />
          </>
        )}

        <span className={styles.buttonLabel}>{children}</span>

        {trailingIcon && (
          <>
            <Spacer size={8} />
            <Icon size={size === 'sm' ? 16 : 24} type={trailingIcon} />
          </>
        )}
      </>
    );

    return (
      <IconColorProvider colorA={iconColor}>
        <InverseThemeSemantics active={inverseSemantics}>
          {React.createElement(
            component,

            {
              className: clsx([
                className,
                styles.Button,
                styles[size!],
                styles[`size_${size}`],
                styles[`color_${color}`],
                disabled && styles.disabled,
                validating && styles.validating,
                isPressed && styles.active,
              ]),
              disabled,
              'aria-disabled': disabled,
              ...mergeProps(pressProps, focusProps, hoverProps, otherProps),
              ref: externalRef,
            },

            content,
          )}
        </InverseThemeSemantics>
      </IconColorProvider>
    );
  },
);

export const ButtonV2 = /* @__PURE__ */ Object.assign(ButtonV2Components.button, ButtonV2Components);
